body {
	font-family: raleway, sans-serif;
	font-weight: 200;
	font-size: 16px;
	color: #5c4b51;
	background-color: #f5f4f4;
	line-height: 1.6; }

//variables
$brown: #5c4b51;
$red: #f06060;
$blue: #8cbeb2;
$yellow: #f3b562;
$grey: #ada2a4;
$bgGrey: #f5f4f4;

@include font('bitter400', bitter-regular);
@include font('bitter400ital', bitter-italic);
@include font('bitter600', bitter-bold);

@mixin h400 {
	font-family: 'bitter400', Arial, sans-serif; }

@mixin h400i {
	font-family: 'bitter400ital', Arial, sans-serif; }

@mixin h600 {
	font-family: 'bitter600', Arial, sans-serif; }


//typography
h1 {
	font-size: 32px; }

h2 {
	font-size: 28px; }

h3 {
	font-size: 26px; }

h4 {
	font-size: 24px; }

h5 {
	font-size: 16px; }

h6 {
	font-size: 13px; }

a {
	display: block;
	color: $brown;
	text-decoration: none;
	cursor: pointer;
	@include tr(all .3s ease-in-out); }

p {
	line-height: 1.6; }

img {
	display: block; }

// common blocks
.i-weight-100 {
	font-weight: 100; }

.i-weight-200 {
	font-weight: 200; }

.i-weight-300 {
	font-weight: 300; }

.i-weight-400 {
	font-weight: 400; }

.i-weight-500 {
	font-weight: 500; }

.i-weight-600 {
	font-weight: 600; }

.i-weight-700 {
	font-weight: 700; }

.is-blue {
	background-color: $blue; }

.is-red {
	background-color: $red; }

.is-yellow {
	background-color: $yellow; }

.is-brown {
	color: $brown; }

.is-white {
	color: #fff; }

.i-bold {
	font-family: 'Bitter', serif;
	font-weight: 600; }

.i-ital {
	font-style: italic; }

.is-active {
	color: $red; }

.i-text-upper {
	text-transform: uppercase; }

.i-text-center {
	text-align: center; }

.i-text-left {
	text-align: left; }

.i-text-right {
	text-align: right; }

.i-mb-55 {
	margin-bottom: 55px; }

.i-pt-20 {
	padding-top: 20px; }

.i-top-bottom-50 {
	padding: 50px 0; }

.bg-white {
	background-color: #fff; }

.bg-grey {
	background-color: $bgGrey; }

.insta {
	padding: 50px 0;
	border-bottom: 2px solid #fff;

	&__top {
		text-align: center; }

	&__descr {
		color: $grey; } }

.cart,
.currency {
	font-family: raleway, sans-serif;
	font-weight: 400;
	color: $brown;
	cursor: pointer;
	position: relative;
	top: 55px;
	@include tr(all .3s ease-in-out);
	&:hover {
		color: $red; } }

.cart {
	i {
		font-size: 25px; } }

.section-title {
	position: relative;
	text-align: center;
	padding-bottom: 22px; }

.sale {
	width: 100%;
	position: relative;
	cursor: pointer;
	box-sizing: border-box;

	&__title {
		padding-top: 30px; }

	&__descr {
		font-family: 'Bitter', serif;
		font-weight: 600;
		font-size: 23px; }

	&__info {
		position: relative; }

	&__offer {
		position: absolute;
		top: 55%;
		right: 16px;
		font-size: 19px;
		font-family: 'Bitter', serif;
		font-weight: 600;

		&:after {
			content: '';
			position: absolute;
			bottom: -10px;
			right: 0;
			height: 3px;
			width: 400px;
			background-color: $yellow; }
		&.is-text-blue {
			color: $blue;
			&:after {
				background-color: $blue; } } } }


.product-big {
	position: relative;
	width: 100%;
	float: left;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	height: 540px;
	text-align: center;
	border: 3px solid #fff;
	border-left: none;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $red;
		opacity: 0;
		transition: opacity .3s ease-in-out; }

	&:hover {
		&:before {
			opacity: .2; } }

	&.bg-fruits {
		background-image: url("../img/fruits.jpg"); }

	&.bg-rasper {
		background-image: url("../img/raspber.jpg"); } }

.quantity {
	position: relative;
	display: inline-block;
	right: 60px;
	margin: 0 20px;
	width: 80px;
	height: 40px;
	line-height: 40px;
	background-color: #f5f4f4;

	&:after {
		position: absolute;
		content: '';
		right: -16px;
		top: 11px;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 8px 0 8px 12px;
		border-color: transparent transparent transparent #ada2a4; }

	&:before {
		position: absolute;
		content: '';
		left: -16px;
		top: 11px;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 8px 12px 8px 0;
		border-color: transparent #ada2a4 transparent transparent; } }

.overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $brown;
	z-index: 5;
	transition: opacity 450ms ease-in-out, visibility 450ms ease-in-out;
	display: none;

	&.is-visible {
		opacity: .6;
		display: block; } }

.mobile-menu {
	width: 320px;
	height: 100%;
	background-color: #fff;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 50;

	&__el {
		width: 100%;
		padding: 15px 38px 14px 38px;
		color: $brown;

		&:hover {
			background-color: $bgGrey; } }

	img {
		margin: 0 auto; } }













